.question{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.singleQuestion{
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid rgb(25, 159, 221);
    padding: 20px;
    margin-top: 10px;
}

.options{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    align-items: center;
    justify-content: space-around;

}

.singleOption{
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    box-shadow: 0 0 2px black;
    border-color: black;
}

@media(max-width:614px){
    .singleOption{
        width: 100%;
    }
}

.select{
    background-color: rgb(16, 124, 12);
    color: white;
    box-shadow: 0 0 1px black;
}

.wrong{
    background-color: rgb(216, 6, 6);
    color: black;
    box-shadow: 0 0 1px black;
}

.controls{
    width: 100%;
    display: flex;
    justify-content: space-around;
    
}